import { useContext, useState } from 'react'
import { Context } from '../Provider/Context'

/**
 * Returns an object containing the active tab index, a function to set the active tab index, and a function to change the active tab index.
 * @returns An object containing the active tab index, a function to set the active tab index, and a function to change the active tab index.
 */
export const _useTab = () => {
  const [activeTab, setActiveTab] = useState<string | number>(0)

  const changeTab = ({ tabId }: { tabId: string | number }) => setActiveTab(tabId)

  return {
    activeTab,
    setActiveTab,
    changeTab,
  }
}

export const useTab = () => {
  const context = useContext(Context)

  if (context === null) {
    throw new Error('useTab must be used within a TabProvider')
  }

  return context
}